.draggable-demo {
  padding: 0 20px;
}
.draggable-demo .draggable-container {
  margin: 10px 30px;
  width: 500px;
  height: 300px;
  overflow: auto;
  border: 1px solid #ccc;
}
.draggable-demo .draggable-container .rc-tree-item-wrap {
  margin-top: 15px;
  height: 37px;
  background-color: seagreen;
}
.draggable-demo .draggable-container .rc-tree-item-wrap > .rc-tree-node-content-wrapper,
.draggable-demo .draggable-container .rc-tree-node-content-wrapper > .rc-tree-title {
  display: inline-block;
  height: 100%;
  width: 100%;
}
.draggable-demo .draggable-container .drag-over-gap-top > .rc-tree-item-wrap {
  border-top: 1px solid green;
}
.draggable-demo .draggable-container .drag-over-gap-bottom > .rc-tree-item-wrap {
  border-top: 1px solid red;
}
.draggable-demo .draggable-container .drag-over > .rc-tree-item-wrap {
  background-color: seashell;
}



